<template>
  <div class="index-wrap clearfix">
    <div class="index-left">
      <div class="index-left-top pro">
        <h2>全部产品</h2>
        <template v-for="prdt in productList">
            <h3>{{prdt.title}}</h3>
            <ul class="product-lists" v-for="item in prdt.list">
              <li>
                <a :href="item.url">{{item.name}}</a>
                <span v-if="item.hot" class="hot-icon">HOT</span>
              </li>
            </ul>
          <div class="sep" v-if="!prdt.last"></div>
        </template>
      </div>

      <div class="index-left-bottom news">
        <h2>最新消息</h2>
        <ul>
          <li v-for="item in newsList">
            <a :href="item.url" class="new-item">{{ item.title }}</a>
          </li>
        </ul>

      </div>
    </div>
    <div class="index-right"></div>
  </div>
</template>
<script>
  export  default {
    name:'index-view',
    data() {
      return {

        productList: [
          {
            title:'PC产品',
            list:[
              {
                name: '数据统计',
                url: 'http://starcraft.com'
              },
              {
                name: '数据预测',
                url: 'http://warcraft.com'
              },
              {
                name: '流量分析',
                url: 'http://overwatch.com',
                hot: true
              },
              {
                name: '广告发布',
                url: 'http://hearstone.com'
              }
            ]

          },
          {
            title: '手机应用类',
            last: true,
            list: [
              {
                name: '91助手',
                url: 'http://weixin.com'
              },
              {
                name: '产品助手',
                url: 'http://twitter.com',
                hot: true
              },
              {
                name: '智能地图',
                url: 'http://maps.com'
              },
              {
                name: '团队语音',
                url: 'http://phone.com'
              }
            ]

          }

        ],
        newsList: [
          {
            title: '智能地图',
            url: 'http://maps.com'
          },
          {
            title: '团队语音',
            url: 'http://phone.com'
          }
        ],


      }

      }

    }

</script>

<style scoped lang="scss">
  @import "../public/common";

  $nav-height: 90px;
  $theme-color:darkgreen;

  .index-wrap {
    width: 100%;
  }

  .index-left {
    float: left;
    width: 20%;
    padding-right: 10px;
    .index-left-top {
      margin-bottom: 10px;
      padding-bottom: 10px;
      background-color: white;

    }
    .index-left-bottom {
      background-color: white;
      padding-bottom: 10px;
    }

    h2 {
      padding-left: 10px;
      height: 35px;
      line-height: 35px;
      color: white;
      font-size: 17px;
      font-weight: 400;
      background-color: $theme-color;

    }
    h3 {
      height: 40px;
      line-height: 40px;
      color: #333;
      font-size: 15px;
      font-weight: 500;
      padding-left: 10px;
    }

    li {
      a{
        color: #666;
        padding-left: 10px;
        font-size: 14px;
      }
      .hot-icon {
        color: red;
        font-weight: 800;
        font-size: 14px;
      }
    }
    .sep {
      height: 10px;
      border-bottom: solid 1px #f0f0f0;
    }
  }
  .index-right {
    float: left;
    width: 80%;
    height: 600px;
    background-color: wheat;

  }

</style>



